<template>
    <div class="container-fluid ">
        <div class="row py-5 rouned-sm justify-content-center">
            <div class="col-lg-6 py-5" id="register">
                <h1 class="text-center">Register Now</h1>
                <div  class="text-start ">
                    <dynamic-form :form="form" @change="valueChanged" />
                </div>
                <!-- btn-sm col-12 col-sm-6 col-lg-6 col-xl-6 mt-5 -->
                <button submit="true" :form="form.id" class="btn rounded-pill btn-outline-secondary btn-sm col-12 col-sm-6 col-lg-6 col-xl-6 mt-5 mb-3">
                    Submit
                </button>
                <div>
                    Already Registered. 
                    <router-link :to="{ name: 'login' }">
                        <label >Login</label>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { computed, defineComponent  } from 'vue';

import {  
  TextField,
  EmailField,
  CheckboxField
} from '@asigloo/vue-dynamic-forms';
console.log(CheckboxField)
export default defineComponent({
  name: 'Register',
  setup() {
    const form = computed(() => ({
      id: 'registerForm',
      fields: {
        name: TextField({
          label: 'Name',
        }),
        email: EmailField ({
            label: 'Email',
        }),
        password: TextField({
            label: 'Password',
        }),
        confirmPassword: TextField({
            label: 'Confirm Password',
        }),
        agree: CheckboxField({
            label: 'I agree to the Terms of Service and Privacy Policy',
        }),
      },
    }));

    function valueChanged(values) {
      console.log('Values', values);
    }

    return {
      form,
      valueChanged,
    };
  },
});
</script>

<style scoped>
#register{
    background: white
}

</style>